import React, { useState } from "react";
import { GedcomX } from 'gedcomx-js';  // Using gedcomx-js as an example parser

function App() {
  const [file, setFile] = useState(null);
  const [results, setResults] = useState(null);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!file) {
      alert("Please upload a GEDCOM file.");
      return;
    }

    const reader = new FileReader();

    reader.onload = async (event) => {
      const gedcomData = event.target.result;

      try {
        // Use GedcomX parser to parse the GEDCOM file
        const gedcomXParser = new GedcomX();
        const parsedData = gedcomXParser.parseGedcom(gedcomData);

        // Extract couples with marriage events from the parsed data
        const couplesWithMarriageEvents = extractMarriageEvents(parsedData);

        // Send extracted couples to the backend for comparison with Google Sheets
        const response = await fetch(
          "https://us-central1-gendep82-matching.cloudfunctions.net/compareCouples",  // Replace with your Cloud Function URL
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ couples: couplesWithMarriageEvents }),
          }
        );

        const data = await response.json();
        setResults(data);
      } catch (error) {
        console.error("Error parsing GEDCOM file or matching with Google Sheets:", error);
      }
    };

    reader.readAsText(file);  // Ensure the file is read as text
  };

  const extractMarriageEvents = (parsedData) => {
    const couples = [];

    // Loop through the parsed data to find individuals and their marriage events
    parsedData.persons.forEach(person => {
      const marriages = person.getFactsByType('Marriage');
      marriages.forEach(marriage => {
        const spouse = marriage.getSpouse();

        if (spouse && marriage.place) {
          couples.push({
            husbandFirstName: person.getGivenName(),
            husbandLastName: person.getSurname(),
            wifeFirstName: spouse.getGivenName(),
            wifeLastName: spouse.getSurname(),
            marriagePlace: marriage.place.name,  // Assuming place has a 'name' field
          });
        }
      });
    });

    return couples;
  };

  return (
    <div>
      <h1>Upload GEDCOM File</h1>
      <form onSubmit={handleSubmit}>
        <input type="file" accept=".ged" onChange={handleFileChange} />
        <button type="submit">Upload and Compare</button>
      </form>

      {results && (
        <div>
          <h2>Comparison Results:</h2>
          <pre>{JSON.stringify(results, null, 2)}</pre>
        </div>
      )}
    </div>
  );
}

export default App;